@font-face {
  font-family: headertype;
  src: URL("../Pokemon\ Solid.ttf") format("truetype");
}

.navbar_cont {
  background-color: rgb(48, 48, 48);
  padding-top: 10px;
  position: relative;
  z-index: 2;
  /* max-width: 1224px; */
  min-width: 427px;
}
a {
  color: rgb(255, 255, 255);
}
a:hover {
  color: rgb(0, 221, 255);
}

.navbar {
  margin: auto;
  margin-top: 15px;
  max-width: 1224px;
  min-width: 427px;
  max-height: 55px;
  background-color: rgb(68, 68, 68);
  border-radius: 10px;
}
.links_nav {
  width: 50%;
  font-size: 20px;
  height: 30px;
  margin: auto;
  display: flex;
  padding-right: 30px;
  justify-content: space-between;
}
.links {
  color: red;
}

.h123 {
  width: 100px;
  background-color: rgb(0, 136, 255);
  text-align: left;
}
.searchInputs {
  display: flex;
}
.serchcont {
  width: 100%;
  color: white;
}
.search {
  width: 400px;
  height: 25px;
}
.search input[type="text"] {
  border: 0;
  position: relative;
  border-radius: 15px;
  height: 30px;
  width: 99%;
  background-color: rgb(109, 108, 108);
  color: white;
  font-size: 18px;
  padding-left: 8px;
  padding-right: 8px;
}
::placeholder {
  color: white;
}
.serach_svg {
  margin-left: -32px;
  z-index: 2;
}
.searchIcon {
  height: 60px;
  width: 50px;
  display: grid;
  place-items: center;
}

input:focus {
  outline: none;
}
.searchIcon svg {
  font-size: 35px;
}

.dataResult {
  margin-top: 0px;
  width: 100%;
  height: 200px;
  font-size: 18px;
  border-radius: 10px;
  background-color: rgb(99, 136, 153);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
  padding-right: 2px;
}

.dataResult::-webkit-scrollbar {
  display: none;
}

.dataResult .dataItem {
  width: 98%;
  height: 50px;
  padding-right: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: rgb(255, 255, 255);
}
.dataItem:hover {
  background-color: rgba(211, 211, 211, 0.132);
  cursor: pointer;
  font-size: 19px;
}
.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}
#scrolltotop {
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: rgb(205, 100, 100);
  color: white;
  cursor: pointer;
  padding: 10px;
  border-radius: 4px;
}

@media screen and (min-width: 850px) {
  .navbar {
    display: flex;
    max-height: 55px;
  }
  .links_nav {
    width: 50%;
  }
  .navbar_cont {
    max-height: 140px;
  }
  .serchcont {
    width: 250px;
  }
  .search {
    margin-top: -5px;
    margin-left: -45px;
    margin-right: auto;
    width: 100%;
  }
}

@media screen and (max-width: 850px) {
  .navbar {
    max-height: 105px;
  }
  .links_nav {
    width: 70%;
  }
  .navbar_cont {
    max-height: 190px;
  }
  .search {
    margin-top: -10px;
    margin-left: -20px;
    width: 100%;
    height: 50px;
  }
}
